.switch__container{
  background:#808fc7;
  width:100%;
  height:100vh;
  margin:auto;
  display:flex;
  justify-content:center;
  align-items:center;
}
.switch {
  background: rgb(175, 174, 174);
  width: 90px;
  height: 40px;
  display: inline-block;
  border-radius: 50px;
  position: relative;
  padding: 0.3rem 0;
  transition: 0.5s ease-in;
  overflow: hidden;
}
.switch > input {
  opacity: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  cursor: pointer;
  z-index: 2;
}
.slider {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  transition: 0.3s ease-in;
  background-size: cover;
}
.slider::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: #FFF;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  transition: 0.3s ease-in;
    transform: translate(10%, 5%);
  border: 2px solid #e8e8ea;
}

.switch > input:checked ~ .slider::before {
  animation: ball 0.6s ;
  left: 44%;
  background: #fffaa8;
  border-color:#f5eb71;
}
@keyframes ball {
  0% {
    width: 40px;
  }
  50% {
    width: 60px;
  }
  100% {
    width: 40px;
  }
}
